umbotron\r\n   */\r\n\r\n   .hero-image {\r\n    width: 100%;\r\n    height: 100%;\r\n    background-size: cover;\r\n    object-fit: cover;\r\n  }\r\n\r\n\r\n/*\r\n  Main Content\r\n*/\r\n\r\nmain {\r\n  padding: 32px;\r\n  flex: 1;\r\n}\r\n\r\n.content {\r\n  margin: 0 auto;\r\n  min-height: 100%;\r\n}\r\n\r\n.content .content__heading {\r\n  font-weight: normal;\r\n}\r\n\r\n.tagline {\r\n  text-align: center;\r\n}\r\n\r\n#mainContent button {\r\n  min-height: 44px;\r\n  min-width: 44px;\r\n  background-color: #e0ac1c;\r\n}\r\n\r\n#mainContent button:hover {\r\n  opacity: 80%;\r\n}\r\n\r\n#mainContent button a {\r\n  text-decoration: none;\r\n  color: white;\r\n  font-size: 16px;\r\n  display: block;\r\n  min-height: 44px;\r\n  min-width: 44px;\r\n}\r\n\r\n/*\r\n  restaurants\r\n*/\r\n.list__Resto {\r\n  color: #616161;\r\n  font-size: 32px;\r\n  text-align: center;\r\n  text-decoration: underline;\r\n}\r\n\r\n.restaurants {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 16px;\r\n  margin: 32px 0;\r\n}\r\n\r\n\r\n/*\r\n  Restaurant Item\r\n*/\r\n\r\n.restaurant-item {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n}\r\n\r\n.restaurant-item__header {\r\n  position: relative;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__poster {\r\n  width: 100%;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating {\r\n  position: absolute;\r\n  padding: 8px;\r\n  bottom: 20px;\r\n  left: 0;\r\n  display: inline-block;\r\n  background-color: black;\r\n  color: white;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {\r\n  margin-left: 10px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content {\r\n  padding: 16px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 {\r\n  margin: 0 0 10px 0;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content